.nav-ul {
    list-style: none;
    display: flex;
    flex-flow: row nowrap;
}
.nav-ul > li {
    padding: 1rem .8rem;
}
.nav-ul > li:hover {
    color: #ff00ff;
}

@media (max-width: 768px) {
    .nav-ul {
        transform: translateX(200%);
        flex-flow: column nowrap;
        background: #83077d;
        position: fixed;
        top: 0;
        right: 0;
        height: 40vh;
        width: 35vw;
        padding-top: 3rem;
        transition: all 0.3s ease-in-out;
    }
    .show.nav-ul {
        transform: translateX(0);
    }
    .nav-ul > li {
        color: #fff;
    }
}