
.recipe-card-div {
    max-width: 24rem;
    margin: 2rem 0;
    border-radius: 0.25rem;
    background: #fff;
    overflow: hidden;
    box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
                0 2px 4px -1px rgba(0, 0, 0, 0.07);;
    position: relative;
}
.recipe-card-div:hover {
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
                0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.recipe-image {
    width: 100%;
    height: 10rem;
    object-fit: cover;
}

.recipe-card-content {
    padding: 1rem 2rem;
}

.recipe-title {
    color: #ff00ff;
    text-align: center;
    padding-bottom: 1rem;
}

.recipe-details {
    font-weight: bold;
    padding: 1rem 0;
}

.recipe-ingre {
    list-style: none;
    line-height: 1.2;
}
.recipe-ingre > p {
    text-align: center;
    font-size: 1.25rem;
    font-weight: bold;
    padding: 0.75rem;
}
.recipe-ingre > li {
    padding: 0.25rem;
    text-indent: -0.7rem;
}
.recipe-ingre > li::before {
    content: "âœ”";
}

@media (min-width: 768px) {
    .recipe-image {
        height: 15rem;
    }
}