.searchbar-div {
    max-width: 20rem;
    border-radius: 0.25rem;
    overflow: hidden;
    margin: 2.5rem auto;
    display: grid;
    place-items: center;
}

.searchbar-form {
    width: 100%;
    max-width: 20rem;
}

.searchbar {
    display: flex;
    align-items: center;
    padding: 0.5rem 0;
}

.searchbar-input {
    appearance: none;
    background: transparent;
    border: none;
    width: 100%;
    color: #4a5568;
    /* border: none solid #ff00ff; */
    border: 4px solid #ff00ff;
    margin-right: 0.75rem;
    padding: 0.25rem 0.5rem;
    line-height: 1.5;
}
.searchbar-input:focus {
    outline: none;
}

.searchbar-button {
    padding: 0.25rem 0.5rem;
    background: #83077d;
    color: white;
    border: 4px solid #83077d;
    flex-shrink: 0;
}
.searchbar-button:hover {
    border-color: #ff00ff;
}