.loader-div {
    display: grid;
    place-items: center;
}

.loader {
    width: 150px;
    height: 150px;
    border-radius: 100%;
    border: 3px solid #3f51b5;
    border-top: 4px solid #ff00ff;
    animation: spin 1s infinite linear;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}