.burger-div {
    width: 2rem;
    height: 2rem;
    position: fixed;
    top: 2rem;
    right: 20px;
    z-index: 5;
    display: none;
}
@media (max-width: 768px) {
    .burger-div {
        display: flex;
        justify-content: space-around;
        flex-flow: column nowrap;
    }
}
.burger-div > div {
    width: 2rem;
    height: 0.25rem;
    background: #333;
    border-radius: 0.5rem;
    transform-origin: 1px;
    transition: all 0.3s linear;
}
.burger-active.burger-div > div {
    background: #f1f1f1;
}
.burger-active.burger-div > div:nth-child(1) {
    transform: rotate(45deg);
}
.burger-active.burger-div > div:nth-child(2) {
    transform: translateX(100%);
    opacity: 0;
}
.burger-active.burger-div > div:nth-child(3) {
    transform: rotate(-45deg);
}