.container {
    margin: 2rem auto;
    padding: 0 4rem;
    max-width: 100%;
    overflow: hidden;

}

.intro {
    text-align: left;
    line-height: 1.4;
    margin: 2rem 0;
}
.recipe {
    color: #ff00ff;
    font-size: 1.2rem;
}
.intro > h3 {
    margin: 2rem 0 0;
    text-align: center;
    text-transform: uppercase;
}

.recipe-card {
    display: grid;
    /* gap: 1rem; */
}


@media (min-width: 640px) {
    .recipe-card {
        grid-template-columns: repeat(1, minmax(0, 1fr));
    }
}
@media (min-width: 768px) {
    .recipe-card {
        grid-template-columns: repeat(3, minmax(0, 1fr));
    }
}
